import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
import { requiredRule } from 'utils/validateRules';

@Form.create({ name: 'FormSearchStuForUnionsMem' })
class FormSearchStuForUnionsMem extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    udpateQueryParams();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            form={this.props.form}
            optionFieldDecorator={{
              trainTypeId: {
                rules: [requiredRule],
              },
              facultyId: {
                rules: [requiredRule],
              },
              courseId: {
                rules: [requiredRule],
              },
              studyFieldId: {
                rules: [requiredRule],
              },
            }}
          />
          <Col span={6}>
            <Form.Item label="Mã SV">{getFieldDecorator('studentCode', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Họ tên">
              {getFieldDecorator('studentFullName', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStuForUnionsMem;



