import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { Card, Row, Col, Button } from 'antd';
import { TableData, CartHeadAction } from 'components/common';
import { BtnExport } from 'components/common/button';
import {
  getStudentsForUnionsMem,
  updateUnionDate,
  exportUnionStudents,
} from 'services/studentForUnionsMem';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import FormSearchStuForUnionsMem from './FormSearchStuForUnionsMem';
import './style.scss';

class StuUnionsMemPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleKhaiTru = async ({ id, unionDate }) => {
    try {
      await updateUnionDate({ id, unionDate });
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      console.log(error);
    }
  };

  handleExport = async ({
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    studentCode,
    studentFullName,
  }) => {
    try {
      await exportUnionStudents({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
        studentCode,
        studentFullName,
      });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'studyFieldName',
        key: 'studyFieldName',
      },
      {
        title: 'Khóa học',
        dataIndex: 'courseName',
        key: 'courseName',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'facultyName',
        key: 'facultyName',
      },
      {
        title: 'Tên hệ',
        dataIndex: 'trainTypeName',
        key: 'trainTypeName',
      },
      {
        title: 'Ngày kết nạp',
        dataIndex: 'unionDate',
        render: (text, record) => {
          if (record.unionDate == null) {
            return <Fragment />;
          }
          return <Fragment>{dateToString(record.unionDate, 'DD/MM/YYYY')}</Fragment>;
        },
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => {
          if (record.unionDate) {
            return (
              <Fragment>
                <Button
                  className="btnEventStuUnionsMem btnKhaiTru"
                  type="primary"
                  onClick={() => this.handleKhaiTru({ id: record.id, unionDate: '' })}
                >
                  Khai trừ
                </Button>
              </Fragment>
            );
          }
          return (
            <Link
              to={{
                pathname: routeConstant.DOAN_HOI__DOAN_VIEN__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              }}
            >
              <Button className="btnEventStuUnionsMem btnKetNap" type="primary">
                Kết nạp
              </Button>
            </Link>
          );
        },
      },
    ];
    const listParamSearch = this.state.paramSearch;
    return (
      <Card title="Đoàn viên là sinh viên">
        <CartHeadAction>
          <BtnExport
            title="Xuất Excel"
            onClick={() =>
              this.handleExport({
                trainTypeId: listParamSearch.trainTypeId,
                facultyId: listParamSearch.facultyId,
                courseId: listParamSearch.courseId,
                studyFieldId: listParamSearch.studyFieldId,
                classId: listParamSearch.classId,
                studentCode: listParamSearch.studentCode,
                studentFullName: listParamSearch.studentFullName,
              })
            }
          />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStuForUnionsMem />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getStudentsForUnionsMem}
              allowGetData={
                !!listParamSearch.trainTypeId &&
                !!listParamSearch.facultyId &&
                !!listParamSearch.courseId &&
                !!listParamSearch.studyFieldId
              }
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StuUnionsMemPage />
  </Page>
);



